.SearchFormButton {
  display: inline-block;
  width: 120px;
  height: 48px;
  border: none;
  border-radius: 4px;
  background-color: #0f1a57;
  color: #ffffff;
  font: inherit;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
  font-family: inherit;
  font-size: 20px;
}

.SearchFormButton:hover {
  background-color: #2ca3f8;
  color: #b0d6f9;
}
